import WorksData from "../works.json";


export const Works = () => {

  const works = [];

  for (const key in WorksData) {
    const workData = WorksData[key];

    const imageStyle = {
      backgroundImage: `url(./img/eyecatch_${key}.png)`
    }

    const work =
            <section key={key} className="section">
              <div className="imageWrapper">
                <div className="image" style={imageStyle}></div>
              </div>
              <div className="info">
                <h3>{workData["title-jp"]}</h3>
                <p><a href={workData["link"]} target="_blank">{workData["link-text"]}</a></p>
                <div className="date">{workData.date}</div>
              </div>
            </section>
    ;

    works.push(work);
  }

  return (
    <div id="component-works">{works}</div>
  );

};
